<template>
  <form
    class="main-cart critical-component-clear"
    action="/cart"
    method="post"
  >
    <div class="container">
      <div class="row">
        <template v-if="cart.item_count > 0 || cartStatus.label === 'adding'">
          <div class="col xs12 l11 offset-l1">
            <h1
              class="main-cart__title h4"
              v-text="$string('cart.title')"
            />
          </div>

          <cart-bundle
            v-if="isMobile && cartBundles?.length > 0"
            :bundles="cartBundles"
            :fromMainCart="false"
            @check-cart-bundle="cartBundles"
          />

          <div class="col xs12 l5 offset-l1">
            <div class="main-cart__line-items">
              <line-item
                v-for="(item) in cartItems"
                :key="getCartItemKey(item)"
                block-class="main-cart"
                :item="item"
                :bundle-discount="bundleDiscount(item)"
              />
            </div>
          </div>

          <div class="col xs12 l4 offset-l1">
            <aside class="main-cart__sidebar">
              <cart-summary
                class="main-cart__summary"
                :payments="payments"
                :partner-feature="partnerFeature"
              />
            </aside>
          </div>
        </template>

        <template v-else>
          <div class="col xs12 m6 offset-m3 text-center">
            <h1
              class="main-cart__title text-heading-2xl text-center"
              v-text="$string('cart.title')"
            />

            <p
              class="main-cart__empty-text text-body-m"
              v-text="$string('cart.empty.text')"
            />

            <btn
              class="main-cart__continue"
              modifiers="primary large"
              :label="$string('cart.continue_shopping')"
              :url="$variable('routes.root')"
              :show-icon="false"
            />
          </div>
        </template>

        <cart-bundle
          v-if="!isMobile && cartBundles?.length > 0"
          :bundles="cartBundles"
          :fromMainCart="true"
          @check-cart-bundle="cartBundles"
        />
      </div>
    </div>

</form>
</template>

<script>

/**
 * Vue: Main cart (main-cart)
 * -----------------------------------------------------------------------------
 * Cart page with line items.
 *
 */
import { mapState } from 'vuex'

import Btn from '~global/btn/btn'
import CartSummary from '~async/main-cart/cart-summary'
import LineItem from '~async/line-item/line-item'
import CartBundle from '~async/cart-bundle/cart-bundle.vue'

export default {
  name: 'MainCart',

  components: {
    Btn,
    CartSummary,
    LineItem,
    CartBundle,
  },

  props: {
    payments: {
      type: Array,
      default: () => ([]),
    },
    partnerFeature: {
      type: [Object, Boolean],
      default: false,
    },
    fromMainCart:{
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      bundles: this.$variable('productBundles'),
      matchingBundles: [],
    }
  },

  computed: {

    /**
     * Map Vuex state.
     */
    ...mapState({
      cart: (state) => state.cart.response,
      cartStatus: (state) => state.cart.status,
      isMobile: (state) => state.index.screen.mobile,
    }),

    /**
     * Compute cart items.
     * - For each item that is being added, check if it's already in the cart.
     * - Check against key (if adding from cart) or id (if adding from other).
     * - If it isn't then add a false item to act as a loading placeholder.
     * @returns {Array}
     */
    cartItems() {
      if (this.cartStatus.label !== 'adding') {
        return this.cart.items
      }

      const placeholders = []

      this.cartStatus.items.forEach((addedItem) => {
        const match = this.cart.items.find((cartItem) => {
          const cartItemProperty = typeof addedItem === 'string'
            ? cartItem.key
            : cartItem.id

          return cartItemProperty === addedItem
        })

        if (match) {
          return
        }

        placeholders.push(false)
      })

      return [
        ...placeholders,
        ...this.cart.items,
      ]
    },

    /**
     * Computes cart bundles
     * @returns {Object}
     */
    cartBundles() {
      if (!this.bundles) {
        return this.matchingBundles
      }

      this.bundles.items.forEach((bundle) => {
        const matchingBundle = this.cartItems.filter((item) => {
          return (bundle.bundleType === 'category' && item.product_type === bundle.bundleCategory)
          || (bundle.bundleType === 'single' && item.product_id === bundle.parentProductId)
        })

        if (matchingBundle.length > 0) {
          this.setMatchingBundle([bundle])
        }
      })

      return this.matchingBundles
    },
  },

  methods: {

    /**
     * Get count of cart items
     */

    getCartItemCount() {
      return this.cartItems.length
    },

    /**
     * Get cart item key.
     * @param {Object} item - Line item.
     * @returns {String}
     */
    getCartItemKey(item) {
      if (!item) {
        return 'cart-line-item-loading'
      }

      return `cart-line-item-${item.key}`
    },

    filterMatchingBundles() {
      this.matchingBundles = this.matchingBundles.filter(bundle => {
        return !this.cartItems.find(cartItem => cartItem.product_id === bundle.id)
      })
    },

    /**
     * Set matching bundle
     * @param {Object} matchingBundle - Matching bundle.
     */
    setMatchingBundle(matchingBundle) {
      matchingBundle.forEach((bundle) => {
        bundle.discountedProducts.forEach((discountProduct) => {
          if (!discountProduct.discountedProduct.available) {
            return
          }

          const hasBundle = this.matchingBundles.find((item) => item.id === discountProduct.discountedProduct.id)

          if (hasBundle) {
            return
          }

          this.matchingBundles.push({
            ...discountProduct.discountedProduct,
            swatchUrls: discountProduct.swatchUrls,
            discountAmount: bundle.discountAmount,
            discountType: bundle.discountType,
            discountAmountSaved: bundle.discountAmountSaved,
          })
        })
      })
      this.filterMatchingBundles()
    },

    /**
     * Get bundle discount percent
     * @param {Object} item - Line item
     * @return {Number}
     */
    bundleDiscount(item) {
      if (!item) {
        return
      }

      return item.properties._buyxgety || false
    },
  },
}
</script>

<style lang="scss">
@import './main-cart';
</style>
